@import '../../../../includes/assets/css/consts.scss';
@import '../base/mixins';

.dpit-theme-minimal {

	.dpit-button {
		padding: unit(5) unit(23);
		font-size: unit(calc(38 * var(--num-size-coef)));
		color: rgba(var(--accent-contrast-inv-color), 0.85);
		font-weight: 600;
		text-transform: uppercase;

		&:not(.dpit-disabled) {

			&:focus {
				color: rgb(var(--accent-color), 0.85);
			}

			&:active,
			&:hover,
			&.hovered {
				color: rgb(var(--accent-color), 0.85);

				&:focus {
					color: rgb(var(--accent-color));
				}
			}
		}
	}

	.dpit-tooltip-buttons {
		padding-top: unit(10);
	}

	.dpit-bullets {
		ul {
			&.dpit-has-scrollbar {
				margin-bottom: unit(10);
			}

			@media (hover: hover) {

				&::-webkit-scrollbar-track {
					background-color: rgba(var(--accent-color), 0.1);
				}

				&::-webkit-scrollbar-thumb {
					background-color: rgba(var(--accent-color), 0.6);
				}
			}
		}
	}

	&.dpit-light-accent {
		.dpit-button {
			color: rgba(var(--accent-contrast-inv-color), 0.8);
		}
	}



}